@charset "UTF-8";
:root {
  --font-body-color: rgb(255, 255, 255);
  --font-color-second: rgb(77, 91, 206);
  --font-color-title: rgb(136, 192, 255);
  --font-color-active: rgb(119, 150, 243);
  --font-color-not-active: rgba(139, 176, 255, 0.424);
  --background-color: rgb(14, 14, 14);
  --background-second: rgb(2, 10, 16);
  --background-selected: rgba(43, 45, 65, 0.337);
  --background-terminal: rgb(22, 22, 30);
  --background-side-bar: rgb(25, 27, 39);
  --background-active-bar: rgb(30, 32, 46);
  --background-color-title: rgb(14, 14, 19);
  --background-transparent: rgba(14, 14, 19, 0.855);
  --selected-color: rgb(113, 203, 255);
  --themes-color: rgb(94, 77, 168);
  --background-thumb: rgb(22, 22, 30);
  --scrollbar-thumb-bgcolor: rgb(71, 83, 45);
  --border-color: rgba(60, 84, 141, 0.286);
  --color-active: rgb(4, 217, 255);
  --pos: center center;
  --size: 0;
  --inset-top: 0;
  --inset-right: 100%;
  --inset-bottom: 100%;
  --inset-left: 0;
  --stripe-width: 10px;
  --nav-marker-border: rgb(59, 134, 134);
  --sidebar-width: 28.5rem;
  --duration: 0.3s;
  --button-size: 4rem;
  --mob-expanded-width: 320px;
  --mob-collapsed-width: 4rem;
}

:root {
  --swiper-scrollbar-top: auto;
  --swiper-scrollbar-bg-color: rgba(0, 0, 0, 0.1);
  --swiper-scrollbar-drag-bg-color: rgba(0, 0, 0, 0.5);
  --swiper-scrollbar-size: 0.4rem;
  --swiper-scrollbar-bottom: 0.4rem;
  --swiper-scrollbar-sides-offset: 1%;
  --swiper-scrollbar-border-radius: 1rem;
  --swiper-theme-color: $themeColor;
  --swiper-navigation-size: 3rem;
  --swiper-pagination-color: #f65e38;
  --swiper-pagination-bullet-opacity: 1;
  --swiper-pagination-bullet-size: 1.2rem;
  --swiper-pagination-bullet-border-radius: 50%;
  --swiper-pagination-bullet-inactive-opacity: 0.4;
  --swiper-pagination-bullet-inactive-color: rgb(0, 0, 0);
  --swiper-pagination-bullet-width: 1.2rem;
  --swiper-pagination-bullet-height: 1.2rem;
  --swiper-pagination-top: auto;
  --swiper-pagination-bottom: 0.5rem;
  --swiper-pagination-bullet-horizontal-gap: 0.5rem;
  --swiper-transition-opacity: 0.9s;
}

.box-shadow {
  box-shadow: 0 1px 10px #020101;
}

@media (min-width: 490.98px) and (max-width: 1920px) {
  .font-size-calc-h2 {
    font-size: calc(28px + 22 * (100vw - 490px) / 1430);
  }
}

.top-box {
  padding: 6rem 0 0;
  padding-top: calc(6rem + 64px) !important;
}
@media (min-width: 1440.99px) {
  .top-box {
    padding: 10rem 0;
    min-height: 100vh;
  }
}

.pagination, .showreel-slide__pagination {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  margin-top: 6rem;
}
.pagination .swiper-pagination-bullet, .showreel-slide__pagination .swiper-pagination-bullet {
  width: 15px;
  height: 15px;
  content: "";
  border-radius: 50%;
  background: #2a3514;
  transition: background 0.5s ease-in-out;
}
.pagination .swiper-pagination-bullet:not(:last-child), .showreel-slide__pagination .swiper-pagination-bullet:not(:last-child) {
  margin-right: 2rem;
}
.pagination .swiper-pagination-bullet-active, .showreel-slide__pagination .swiper-pagination-bullet-active {
  width: 15px;
  height: 15px;
  content: "";
  border-radius: 50%;
  background: #9acd32;
  scale: 1.2;
  transition: scale 0.5s ease-in-out;
}
.pagination.swiper-pagination-lock, .swiper-pagination-lock.showreel-slide__pagination {
  display: none;
}

.grad_underline {
  /* Определяем фоновые изображения */
  transition: background-size 400ms;
  /* Размер фона для каждого слоя */
  background-image: linear-gradient(to left, transparent, transparent), linear-gradient(to left, var(--background-selected), var(--font-color-active));
  /* Позиционирование фона */
  background-repeat: no-repeat;
  /* Повторение фона */
  background-position: 100% 100%, 0 100%; /* Второе изображение */
  /* Переход для изменения размера фона */
  background-size: 100% 1px, 0 1px; /* Второе изображение */
}

.gradient-black {
  background-image: linear-gradient(-45deg, #1b0d39, #020101 70%);
}

.gradient-neon-revers, .service-description__column {
  background: radial-gradient(at 95% 7%, rgba(50, 38, 98, 0.45) 0px, transparent 50%), radial-gradient(at -15% 102%, rgba(50, 38, 98, 0.45) 0px, transparent 50%);
}

.gradient-neon-color {
  background: radial-gradient(at -15% 1%, rgba(50, 38, 98, 0.45) 0px, transparent 50%), radial-gradient(at 90% 90%, rgba(50, 38, 98, 0.45) 0px, transparent 50%);
}

.gradient-border-before::before {
  content: "";
  height: 1px;
  width: 100%;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  z-index: 1;
  background-image: linear-gradient(to left, #332763 30%, rgb(2, 1, 1) 65%);
}

.gradient-border-before-revers::before {
  content: "";
  height: 1px;
  width: 100%;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  z-index: 1;
  background-image: linear-gradient(to right, #381f47 30%, rgb(2, 1, 1) 65%);
}

.gradient-border-after::after {
  content: "";
  position: absolute;
  right: 0;
  bottom: 0;
  height: 1px;
  width: 100%;
  z-index: 1;
  background-image: linear-gradient(to right, #381f47 30%, rgb(2, 1, 1) 65%);
}

.gradient-border-after-revers::after {
  content: "";
  position: absolute;
  right: 0;
  bottom: 0;
  height: 1px;
  width: 100%;
  z-index: 1;
  background-image: linear-gradient(to left, #381f47 30%, rgb(2, 1, 1) 65%);
}

.gradient-spec {
  background-image: linear-gradient(-45deg, rgb(23, 14, 49) 0%, rgba(254, 89, 194, 0.3), rgb(23, 14, 49) 83%);
  background-size: 200% auto;
  transition: background-position 0.4s ease-in-out;
}
.gradient-spec:hover {
