  background-size: 200% auto;
  transition: background-position 0.4s ease-in-out;
}
.gradient-spec-comfort:hover {
  background-position: right center;
}

.liner-gradient-box {
  background-image: linear-gradient(45deg, #10133a 55%, rgba(71, 32, 138, 0.4509803922) 10%, #10133a)/cover no-repeat;
}

.text-gradient {
  background: currentColor 100%;
  background-clip: text;
  -webkit-text-fill-color: transparent;
}

.gradient-header {
  background-image: linear-gradient(-45deg, rgb(26, 21, 48), rgb(51, 39, 100) 65%, rgb(2, 1, 1) 75%);
}

.gradient-services {
  background-image: linear-gradient(to right, #050202 20%, #1c153f 50%, #050202 80%);
}

.gradient-title {
  background-image: linear-gradient(to top, #170e31 10%, #402e6b 50%, #1a1530 100%);
}

.gradient-spec-active {
  background-image: linear-gradient(-45deg, #04d9ff, #170e31 83%);
  background-size: 200% 200%;
}

.gradient-border-right {
  background-image: linear-gradient(to right, rgb(2, 1, 1) 20%, #332764 50%, #2a3514 80%);
}

.icon-style {
  display: flex;
  align-items: center;
  height: 100%;
  padding: 0 1.5rem;
  border: 1px var(--border-color);
  border-style: none solid;
}

.shadow-after {
  z-index: -1;
  transform: rotateX(15deg) translateZ(-10px);
  color: rgba(23, 14, 49, 0.479);
  text-shadow: 1px 0 1px rgb(26, 21, 48), 0 0 3px rgba(125, 248, 255, 0.26), 0 0 10px rgba(204, 255, 0, 0.267);
}

.overflow-hidden {
  overflow-x: auto;
  -webkit-overflow-scrolling: touch;
  scrollbar-width: none;
}
.overflow-hidden::-webkit-scrollbar {
  height: 0;
}

.border-present {
  position: absolute;
  display: block;
  color: rgb(4, 217, 255);
  background-image: linear-gradient(to right, currentColor 0%, rgb(200, 255, 0) 8%, transparent 8%, transparent 92%, rgb(200, 255, 0) 92%, currentColor 100%), linear-gradient(to right, currentColor 0%, rgb(200, 255, 0) 8%, transparent 8%, transparent 92%, rgb(200, 255, 0) 92%, currentColor 100%), linear-gradient(to bottom, currentColor 0%, rgb(200, 255, 0) 11%, transparent 11%, transparent 89%, rgb(200, 255, 0) 89%, currentColor 100%), linear-gradient(to bottom, currentColor 0%, rgb(200, 255, 0) 11%, transparent 11%, transparent 89%, rgb(200, 255, 0) 89%, currentColor 100%);
  background-repeat: no-repeat;
  background-position: 0 0, 0 100%, 0 0, 100% 0;
  background-size: 100% 1px, 100% 1px, 1px 100%, 1px 100%;
  inset: 0.7rem;
}

.link_underline::before, .service-description__link::before, .link_underline::after, .service-description__link::after {
  position: absolute;
  width: 0;
  height: 1px;
  content: "";
  transition: width 0.4s ease-in-out;
  opacity: 0.6;
  background-color: rgb(4, 217, 255);
}
.link_underline::before, .service-description__link::before {
  right: 0;
  bottom: 0;
}
.link_underline::after, .service-description__link::after {
  bottom: 0;
  left: 0;
}
.link_underline._active::before, ._active.service-description__link::before {
  width: 45%;
}
.link_underline._active::after, ._active.service-description__link::after {
  width: 45%;
}
.link_underline:hover, .service-description__link:hover {
  transition: color 0.4s ease-in-out;
  color: rgb(4, 217, 255);
}
.link_underline:hover::before, .service-description__link:hover::before {
  width: 45%;
}
.link_underline:hover::after, .service-description__link:hover::after {
  width: 45%;
}
.link_underline:hover a, .service-description__link:hover a {
  opacity: 1;
}

.is-open {
  display: grid;
  width: 100%;
  transition: grid-template-rows 0.4s ease-in-out;
  grid-template-columns: 1fr;
  grid-template-rows: 0fr;
}
.is-open._is-open {
  grid-template-rows: 1fr;
}

.gradient-border-bottom {
  background-image: linear-gradient(to left top, rgba(129, 100, 255, 0.61) 45%, rgb(2, 1, 1) 71%);
}

@media (min-width: 920.99px) {
  /* FLOW: RIGHT */
  [data-tooltip] {
    position: relative;
  }
  [data-tooltip]::after {
    content: attr(data-tooltip);
    border-radius: 0.6rem;
    border: 1px solid var(--border-color);
    background-color: rgba(35, 52, 59, 0.8705882353);
    position: absolute;
    padding: 1.2rem;
    color: #fff;
    opacity: 0;
    visibility: hidden;
    transition: all 0.4s ease-in-out;
    font-family: "Manrope", sans-serif;
    font-weight: 200;
    font-size: 1.3rem;
    letter-spacing: 0.2em;
    text-transform: uppercase;
    z-index: 9;
    white-space: nowrap;
  }
  [data-tooltip]::before {
    content: "";
    border: 9px solid transparent;
    border-right: 9px solid var(--font-color-active);
    position: absolute;
    z-index: 9;
    margin-left: -20px;
    bottom: 12%;
    opacity: 0;
    visibility: hidden;
    transition: all 0.4s ease-in-out;
  }
  [data-tooltip]:hover::before,
  [data-tooltip]:hover::after {
    visibility: visible;
    opacity: 1;
    display: block;
  }
  [data-tooltip=""]::before,
  [data-tooltip=""]::after {
    display: none !important;
  }
  [data-tooltip][flow^=right]::after {
    top: 50%;
    left: calc(120rem + 10px);
    transform: translate(0.5em, -50%);
  }
  [data-tooltip][flow^=right]::before {
    top: 50%;
    left: 121rem;
    transform: translate(0.5em, -50%);
  }
  [data-tooltip][flow^=right]:hover::before,
  [data-tooltip][flow^=right]:hover::after {
    animation: data-tooltip 300ms ease-out forwards;
  }
  @keyframes data-tooltip {
    to {
      opacity: 1;
      transform: translate(-110rem, -50%);
    }
  }
}
