* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  border: none;
  scrollbar-width: none;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

img,
svg {
  vertical-align: top;
  user-select: none;
  pointer-events: none;
  width: 100%;
  height: auto;
  max-width: 100%;
}

a {
  cursor: pointer;
  text-decoration: none;
  color: inherit;
}

a:hover {
  text-decoration: none;
}

a:visited {
  vertical-align: middle;
  text-emphasis-style: none;
}

input,
button,
textarea {
  border: none;
}

button,
input[type=submit] {
  display: inline-block;
  cursor: pointer;
  background: transparent none;
  box-shadow: none;
}

input,
textarea,
button,
select {
  font-family: inherit;
  font-size: inherit;
  color: inherit;
  background-color: transparent;
}

input:focus,
input:active,
button:focus,
button:active {
  outline: none;
}

input,
textarea,
button,
select {
  font-family: inherit;
  font-size: inherit;
  color: inherit;
  background-color: transparent;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

label {
  cursor: pointer;
}

a:focus,
a:active,
input:focus,
textarea:focus {
  outline: none;
}

ul,
ol,
li {
  list-style: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: inherit;
  font-style: inherit;
}

::selection {
  color: rgb(155, 153, 153);
  background-color: rgba(0, 84, 99, 0.247);
  text-shadow: 0 1px 0 #000;
}

::-moz-selection {
  color: rgb(155, 153, 153);
  background-color: rgba(0, 84, 99, 0.247);
  text-shadow: 0 1px 0 #000;
}

::-webkit-scrollbar {
  width: 0.5rem;
  padding: 0.031em;
}

::-webkit-scrollbar-track {
  background: var(--scrollbar-thumb-bgcolor);
}

::-webkit-scrollbar-thumb {
  border: 1px solid var(--border-color);
  border-radius: 2px;
  background-color: var(--themes-color);
  background-image: -webkit-linear-gradient(45deg, var(--background-thumb) 25%, transparent 25%, transparent 50%, var(--background-thumb) 50%, var(--background-thumb) 75%, transparent 75%, transparent);
}

::-webkit-scrollbar-thumb:hover {
  background-color: var(--background-thumb);
  background-image: -webkit-linear-gradient(45deg, var(--themes-color) 25%, transparent 25%, transparent 50%, var(--themes-color) 50%, var(--themes-color) 75%, transparent 75%, transparent);
}

